a {
    text-decoration: none; color: #333;
}

.btn {
    position: absolute;
    left: 200px; top: 0px;
    width: 140px; height: 50px;
    color: #333;
    background-color: rgba(255, 255, 0, 50%);
}

nav {
    position: absolute; 
    left: 0px;
    top: 0px; 
    width: 330px;
    height: 50px; background: rgba(255, 255, 255, 50%);
    display: flex;
    flex-flow: column no-wrap;
    transition: all 0.5s;
}

.menu_01, .menu_02, .menu_03{
    position: relative;
    float: left;
    width: 110px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 50%);
    transition: all 0.5s;
}
    .menu_01:hover, .menu_02:hover, .menu_03:hover{
        color: #fff;
        background-color: rgba(0, 0, 0, 50%);
        transition: all 0.5s;
    }

    .menu_01:hover > a, .menu_02:hover > a, menu:hover > a{
        color: #fff;
    }